/* Proyecto Completo - Portal Cliente */
.proyecto-completo {
  max-width: 1100px;
  position: relative;
}

/* Toast */
.toast {
  position: fixed;
  top: 20px;
  right: 20px;
  padding: 12px 20px;
  border-radius: 8px;
  font-size: 0.9rem;
  font-weight: 600;
  z-index: 9999;
  animation: fadeIn 0.3s ease;
  box-shadow: 0 4px 16px rgba(0,0,0,0.15);
}
.toast-success { background: #4caf50; color: white; }
.toast-error   { background: #f44336; color: white; }
.toast-warning { background: #ff9800; color: white; }

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(-10px); }
  to   { opacity: 1; transform: translateY(0); }
}

/* Cabecera */
.proyecto-header {
  margin-bottom: 24px;
}

.back-btn {
  background: none;
  border: none;
  color: #1565c0;
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
  padding: 0;
  margin-bottom: 12px;
  display: inline-block;
}

.back-btn:hover { text-decoration: underline; }

.proyecto-title-row {
  display: flex;
  align-items: center;
  gap: 14px;
  flex-wrap: wrap;
  margin-bottom: 14px;
}

.proyecto-title-row h1 {
  margin: 0;
  font-size: 1.6rem;
  font-weight: 700;
  color: #1a237e;
}

.chat-proyecto-btn {
  background: #1565c0;
  color: white;
  border: none;
  padding: 9px 18px;
  border-radius: 8px;
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s;
}

.chat-proyecto-btn:hover { background: #0d47a1; }

/* Pestañas */
.proyecto-tabs {
  display: flex;
  gap: 4px;
  border-bottom: 2px solid #e0e0e0;
  margin-bottom: 24px;
  flex-wrap: wrap;
}

.tab-btn {
  background: none;
  border: none;
  padding: 10px 18px;
  font-size: 0.9rem;
  font-weight: 600;
  color: #666;
  cursor: pointer;
  border-bottom: 2px solid transparent;
  margin-bottom: -2px;
  transition: color 0.2s, border-color 0.2s;
}

.tab-btn:hover { color: #1565c0; }
.tab-btn.active { color: #1565c0; border-bottom-color: #1565c0; }

/* Info */
.info-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  gap: 18px;
  margin-bottom: 28px;
}

.info-field.full-width {
  grid-column: 1 / -1;
}

.info-field {
  background: white;
  border: 1px solid #e0e0e0;
  border-radius: 10px;
  padding: 14px 16px;
}

.info-field label {
  display: block;
  font-size: 0.78rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  color: #888;
  margin-bottom: 6px;
}

.info-field p {
  margin: 0;
  font-size: 0.95rem;
  color: #212121;
}

/* Equipo */
.empleados-section h3 {
  font-size: 1rem;
  font-weight: 700;
  color: #333;
  margin: 0 0 14px;
}

.empleados-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 12px;
}

.empleado-card {
  background: white;
  border: 1px solid #e0e0e0;
  border-radius: 10px;
  padding: 14px;
  display: flex;
  align-items: center;
  gap: 12px;
}

.empleado-avatar {
  width: 38px;
  height: 38px;
  min-width: 38px;
  background: linear-gradient(135deg, #1a237e, #283593);
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 1rem;
}

.empleado-info {
  display: flex;
  flex-direction: column;
  min-width: 0;
}

.empleado-nombre {
  font-weight: 600;
  font-size: 0.9rem;
  color: #212121;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.empleado-rol {
  font-size: 0.78rem;
  color: #888;
  text-transform: capitalize;
}

/* Tabla */
.table-container {
  background: white;
  border-radius: 10px;
  border: 1px solid #e0e0e0;
  overflow: hidden;
}

.data-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 0.9rem;
}

.data-table th {
  background: #f5f5f5;
  padding: 11px 14px;
  text-align: left;
  font-weight: 700;
  font-size: 0.8rem;
  color: #555;
  text-transform: uppercase;
  letter-spacing: 0.4px;
  border-bottom: 1px solid #e0e0e0;
}

.data-table td {
  padding: 12px 14px;
  border-bottom: 1px solid #f0f0f0;
  color: #333;
}

.data-table tr:last-child td { border-bottom: none; }
.data-table tr:hover td { background: #fafafa; }

/* Botones */
.btn-accept {
  background: #4caf50;
  color: white;
  border: none;
  padding: 6px 14px;
  border-radius: 6px;
  font-size: 0.85rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s;
}

.btn-accept:hover { background: #388e3c; }

.btn-download {
  background: #1565c0;
  color: white;
  border: none;
  padding: 6px 14px;
  border-radius: 6px;
  font-size: 0.85rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s;
}

.btn-download:hover { background: #0d47a1; }

/* Badges */
.badge {
  display: inline-block;
  padding: 3px 10px;
  border-radius: 20px;
  font-size: 0.78rem;
  font-weight: 600;
}

.badge-planificacion { background: #e8eaf6; color: #3949ab; }
.badge-en_progreso   { background: #e3f2fd; color: #1565c0; }
.badge-pausado       { background: #fff8e1; color: #f57f17; }
.badge-completado    { background: #e8f5e9; color: #2e7d32; }
.badge-cancelado     { background: #fce4ec; color: #c62828; }
.badge-enviado       { background: #e3f2fd; color: #1565c0; }
.badge-borrador      { background: #f5f5f5; color: #757575; }

/* Empty tab */
.empty-tab {
  text-align: center;
  padding: 40px 20px;
  color: #888;
}

/* Loading */
.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 60px 20px;
  gap: 14px;
  color: #666;
}

.spinner {
  width: 36px;
  height: 36px;
  border: 3px solid #e0e0e0;
  border-top-color: #1a237e;
  border-radius: 50%;
  animation: spin 0.7s linear infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

@media (max-width: 600px) {
  .proyecto-title-row h1 { font-size: 1.3rem; }
  .info-grid { grid-template-columns: 1fr; }
  .empleados-grid { grid-template-columns: 1fr; }
  .data-table { font-size: 0.8rem; }
  .data-table th, .data-table td { padding: 8px 10px; }
}
